
@import '../../assets/css/common.css';
.usertype-tip{
    font-weight: 600;
    color: #4d6983;
    margin: 80px 0px 30px 0px;
}
.usertype-form{
    padding: 10px 15px;
}
.usertype-radio{
    width: 100%;
    padding: 10px 20px;
    text-align: center;
    border: 1px solid #e4e4e4;
    border-radius: 5px;
    margin: 20px 0px 0px;
}
.usertype-radio-selected{
    background: #49ced4;
    color: #fff;
}



